
p{
    font-family: "Geist Mono", monospace;
    text-align: center;
    font-weight: bold;
    font-size: 30px;
    color: #2c3e50;
    display: block;
    margin-top: 42px;
}

.text-flicker-out-glow {
	-webkit-animation: text-flicker-out-glow 3s cubic-bezier(0.950, 0.050, 0.795, 0.035) infinite both;
	        animation: text-flicker-out-glow 3s cubic-bezier(0.950, 0.050, 0.795, 0.035) infinite both;
}

.text-pop-up-top {
	-webkit-animation: text-pop-up-top 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) infinite both;
	        animation: text-pop-up-top 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) infinite both;
}

